	module.exports = {"data":{"site":{"siteMetadata":{"startDate":"2018-06-06","lessons":8,"noClass":[4]}},"markdownRemark":{"html":"<h3>Selectors</h3>\n<table>\n<thead>\n<tr>\n<th>Selector</th>\n<th>Example</th>\n<th>Example description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>\".class</td>\n<td>.intro</td>\n<td>Selects all elements with class=\"intro\"</td>\n<td>1</td>\n</tr>\n<tr>\n<td>#\nid</td>\n<td>#\nfirstname</td>\n<td>Selects the element with id=\"firstname\"</td>\n<td>1</td>\n</tr>\n<tr>\n<td>*</td>\n<td>*</td>\n<td>Selects all elements</td>\n<td>2</td>\n</tr>\n<tr>\n<td>element</td>\n<td>p</td>\n<td>Selects all \n<code>\n&#x3C;\np\n>\n</code>\n elements</td>\n<td>1</td>\n</tr>\n<tr>\n<td>element,element</td>\n<td>div, p</td>\n<td>Selects all \n<code>\n&#x3C;\ndiv> elements and all \n<code>\n&#x3C;\np\n>\n</code>\n elements</td>\n<td>1</td>\n</tr>\n<tr>\n<td>element element</td>\n<td>div p</td>\n<td>Selects all \n<code>\n&#x3C;\np\n>\n</code>\n elements inside \n<code>\n&#x3C;\ndiv> elements</td>\n<td>1</td>\n</tr>\n<tr>\n<td>element>element</td>\n<td>div > p</td>\n<td>Selects all \n<code>\n&#x3C;\np\n>\n</code>\n elements where the parent is a \n<code>\n&#x3C;\ndiv\n>\n</code>\n element</td>\n<td>2</td>\n</tr>\n<tr>\n<td>element+element</td>\n<td>div + p</td>\n<td>Selects all \n<code>\n&#x3C;\np\n>\n</code>\n elements that are placed immediately after \n<code>\n&#x3C;\ndiv\n>\n</code>\n elements</td>\n<td>2</td>\n</tr>\n<tr>\n<td>element1~element2</td>\n<td>p ~ ul</td>\n<td>Selects every \n<code>\n&#x3C;\nul\n>\n</code>\n element that are preceded by a \n<code>\n&#x3C;\np\n>\n</code>\n element</td>\n<td>3</td>\n</tr>\n<tr>\n<td>[\nattribute\n]</td>\n<td>[\ntarget\n]</td>\n<td>Selects all elements with a target attribute</td>\n<td>2</td>\n</tr>\n<tr>\n<td>[\nattribute=value\n]</td>\n<td>[\ntarget=\n_\nblank\n]</td>\n<td>Selects all elements with target=\"\n_\nblank\"</td>\n<td>2</td>\n</tr>\n<tr>\n<td>[\nattribute~=value\n]</td>\n<td>[\ntitle~=flower\n]</td>\n<td>Selects all elements with a title attribute containing the word \"flower\"</td>\n<td>2</td>\n</tr>\n<tr>\n<td>[\nattribute&#x26;124;=value\n]</td>\n<td>[\nlang&#x26;124;=en\n]</td>\n<td>Selects all elements with a lang attribute value starting with \"en\"</td>\n<td>2</td>\n</tr>\n<tr>\n<td>[\nattribute^=value\n]</td>\n<td>a\n[\nhref^=\"https\"\n]</td>\n<td>Selects every \n<code>\n&#x3C;\na\n>\n</code>\n element whose href attribute value begins with \"https\"</td>\n<td>3</td>\n</tr>\n<tr>\n<td>[\nattribute$=value\n]</td>\n<td>a\n[\nhref$=\".pdf\"\n]</td>\n<td>Selects every \n<code>\n&#x3C;\na\n>\n</code>\n element whose href attribute value ends with \".pdf\"</td>\n<td>3</td>\n</tr>\n<tr>\n<td>[\nattribute*=value\n]</td>\n<td>a\n[\nhref*=\"w3schools\"\n]</td>\n<td>Selects every \n<code>\n&#x3C;\na\n>\n</code>\n element whose href attribute value contains the substring \"w3schools\"</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:active</td>\n<td>a:active</td>\n<td>Selects the active link</td>\n<td>1</td>\n</tr>\n<tr>\n<td>::after</td>\n<td>p::after</td>\n<td>Insert something after the content of each \n<code>\n&#x3C;\np\n>\n</code>\n element</td>\n<td>2</td>\n</tr>\n<tr>\n<td>::before</td>\n<td>p::before</td>\n<td>Insert something before the content of each \n<code>\n&#x3C;\np\n>\n</code>\n element</td>\n<td>2</td>\n</tr>\n<tr>\n<td>:checked</td>\n<td>input:checked</td>\n<td>Selects every checked \n<code>\n&#x3C;\ninput\n>\n</code>\n element</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:disabled</td>\n<td>input:disabled</td>\n<td>Selects every disabled \n<code>\n&#x3C;\ninput\n>\n</code>\n element</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:empty</td>\n<td>p:empty</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element that has no children (including text nodes)</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:enabled</td>\n<td>input:enabled</td>\n<td>Selects every enabled \n<code>\n&#x3C;\ninput\n>\n</code>\n element</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:first-child</td>\n<td>p:first-child</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element that is the first child of its parent</td>\n<td>2</td>\n</tr>\n<tr>\n<td>::first-letter</td>\n<td>p::first-letter</td>\n<td>Selects the first letter of every \n<code>\n&#x3C;\np\n>\n</code>\n element</td>\n<td>1</td>\n</tr>\n<tr>\n<td>::first-line</td>\n<td>p::first-line</td>\n<td>Selects the first line of every \n<code>\n&#x3C;\np\n>\n</code>\n element</td>\n<td>1</td>\n</tr>\n<tr>\n<td>:first-of-type</td>\n<td>p:first-of-type</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element that is the first \n<code>\n&#x3C;\np\n>\n</code>\n element of its parent</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:focus</td>\n<td>input:focus</td>\n<td>Selects the input element which has focus</td>\n<td>2</td>\n</tr>\n<tr>\n<td>:hover</td>\n<td>a:hover</td>\n<td>Selects links on mouse over</td>\n<td>1</td>\n</tr>\n<tr>\n<td>:in-range</td>\n<td>input:in-range</td>\n<td>Selects input elements with a value within a specified range</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:invalid</td>\n<td>input:invalid</td>\n<td>Selects all input elements with an invalid value</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:lang(language)</td>\n<td>p:lang(it)</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element with a lang attribute equal to \"it\" (Italian)</td>\n<td>2</td>\n</tr>\n<tr>\n<td>:last-child</td>\n<td>p:last-child</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element that is the last child of its parent</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:last-of-type</td>\n<td>p:last-of-type</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element that is the last \n<code>\n&#x3C;\np\n>\n</code>\n element of its parent</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:link</td>\n<td>a:link</td>\n<td>Selects all unvisited links</td>\n<td>1</td>\n</tr>\n<tr>\n<td>:not(selector)</td>\n<td>:not(p)</td>\n<td>Selects every element that is not a \n<code>\n&#x3C;\np\n>\n</code>\n element</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:nth-child(n)</td>\n<td>p:nth-child(2)</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element that is the second child of its parent</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:nth-last-child(n)</td>\n<td>p:nth-last-child(2)</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element that is the second child of its parent, counting from the last child</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:nth-last-of-type(n)</td>\n<td>p:nth-last-of-type(2)</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element that is the second \n<code>\n&#x3C;\np\n>\n</code>\n element of its parent, counting from the last child</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:nth-of-type(n)</td>\n<td>p:nth-of-type(2)</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element that is the second \n<code>\n&#x3C;\np\n>\n</code>\n element of its parent</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:only-of-type</td>\n<td>p:only-of-type</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element that is the only \n<code>\n&#x3C;\np\n>\n</code>\n element of its parent</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:only-child</td>\n<td>p:only-child</td>\n<td>Selects every \n<code>\n&#x3C;\np\n>\n</code>\n element that is the only child of its parent</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:optional</td>\n<td>input:optional</td>\n<td>Selects input elements with no \"required\" attribute</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:out-of-range</td>\n<td>input:out-of-range</td>\n<td>Selects input elements with a value outside a specified range</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:read-only</td>\n<td>input:read-only</td>\n<td>Selects input elements with the \"readonly\" attribute specified</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:read-write</td>\n<td>input:read-write</td>\n<td>Selects input elements with the \"readonly\" attribute NOT specified</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:required</td>\n<td>input:required</td>\n<td>Selects input elements with the \"required\" attribute specified</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:root</td>\n<td>:root</td>\n<td>Selects the document's root element</td>\n<td>3</td>\n</tr>\n<tr>\n<td>::selection</td>\n<td>::selection</td>\n<td>Selects the portion of an element that is selected by a user</td>\n<td> </td>\n</tr>\n<tr>\n<td>:target</td>\n<td>#news:target</td>\n<td>Selects the current active #news element (clicked on a URL containing that anchor name)</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:valid</td>\n<td>input:valid</td>\n<td>Selects all input elements with a valid value</td>\n<td>3</td>\n</tr>\n<tr>\n<td>:visited</td>\n<td>a:visited</td>\n<td>Selects all visited links</td>\n<td>1\"</td>\n</tr>\n</tbody>\n</table>\n<h2>Color Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>color</td>\n<td>Sets the color of text</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\ncolor: color \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>opacity</td>\n<td>Sets the opacity level for an element</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nopacity: number \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Background</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>background</td>\n<td>A shorthand property for setting all the background properties in one declaration</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nbackground: bg-color bg-image position/bg-size bg-repeat bg-origin bg-clip bg-attachment initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>background-attachment</td>\n<td>Sets whether a background image is fixed or scrolls with the rest of the page</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nbackground-attachment: scroll \n|\n fixed \n|\n local \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>background-blend-mode</td>\n<td>Specifies the blending mode of each background layer (color/image)</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nbackground-blend-mode: normal \n|\n multiply \n|\n screen \n|\n overlay \n|\n darken \n|\n lighten \n|\n color-dodge \n|\n saturation \n|\n color \n|\n luminosity;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>background-color</td>\n<td>Specifies the background color of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nbackground-color: color \n|\n transparent \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>background-image</td>\n<td>Specifies one or more background images for an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nbackground-image: url \n|\n none \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>background-position</td>\n<td>Specifies the position of a background image</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nbackground-position: value;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>background-repeat</td>\n<td>Sets how a background image will be repeated</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nbackground-repeat: repeat \n|\n repeat-x \n|\n repeat-y \n|\n no-repeat \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>background-clip</td>\n<td>Specifies the painting area of the background</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nbackground-clip: border-box \n|\n padding-box \n|\n content-box \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>background-origin</td>\n<td>Specifies where the background image(s) is/are positioned</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nbackground-origin: padding-box \n|\n border-box \n|\n content-box \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>background-size</td>\n<td>Specifies the size of the background image(s)</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nbackground-size: auto \n|\n length \n|\n cover \n|\n contain \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Border Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>border</td>\n<td>Sets all the border properties in one declaration</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nborder: border-width border-style border-color \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>border-bottom</td>\n<td>Sets all the bottom border properties in one declaration</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nborder-bottom: border-width border-style border-color \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>border-bottom-color</td>\n<td>Sets the color of the bottom border</td>\n<td>1 </td>\n</tr>\n<tr>\n<td>border-bottom-left-radius</td>\n<td>Defines the shape of the border of the bottom-left corner</td>\n<td>3</td>\n</tr>\n<tr>\n<td>border-bottom-right-radius</td>\n<td>Defines the shape of the border of the bottom-right corner</td>\n<td>3</td>\n</tr>\n<tr>\n<td>border-bottom-style</td>\n<td>Sets the style of the bottom border</td>\n<td>1</td>\n</tr>\n<tr>\n<td>border-bottom-width</td>\n<td>Sets the width of the bottom border</td>\n<td>1</td>\n</tr>\n<tr>\n<td>border-color</td>\n<td>Sets the color of the four borders</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nborder-color: color \n|\n transparent \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>border-image</td>\n<td>A shorthand property for setting all the border-image-\n*\n properties</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nborder-image: source slice width outset repeat \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>border-image-outset</td>\n<td>Specifies the amount by which the border image area extends beyond the border box</td>\n<td>3</td>\n</tr>\n<tr>\n<td>border-image-repeat</td>\n<td>Specifies whether the border image should be repeated, rounded or stretched</td>\n<td>3</td>\n</tr>\n<tr>\n<td>border-image-slice</td>\n<td>Specifies how to slice the border image</td>\n<td>3</td>\n</tr>\n<tr>\n<td>border-image-source</td>\n<td>Specifies the path to the image to be used as a border</td>\n<td>3</td>\n</tr>\n<tr>\n<td>border-image-width</td>\n<td>Specifies the widths of the image-border</td>\n<td>3</td>\n</tr>\n<tr>\n<td>border-left</td>\n<td>Sets all the left border properties in one declaration</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nborder-left: border-width border-style border-color \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>border-left-color</td>\n<td>Sets the color of the left border</td>\n<td>1</td>\n</tr>\n<tr>\n<td>border-left-style</td>\n<td>Sets the style of the left border</td>\n<td>1</td>\n</tr>\n<tr>\n<td>border-left-width</td>\n<td>Sets the width of the left border</td>\n<td>1</td>\n</tr>\n<tr>\n<td>border-radius</td>\n<td>A shorthand property for setting all the four border-\n*\n-radius properties</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nborder-radius: 1-4 length \n|\n % / 1-4 length \n|\n % \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>border-right</td>\n<td>Sets all the right border properties in one declaration</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nborder-right: border-width border-style border-color \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>border-right-color</td>\n<td>Sets the color of the right border</td>\n<td>1</td>\n</tr>\n<tr>\n<td>border-right-style</td>\n<td>Sets the style of the right border</td>\n<td>1</td>\n</tr>\n<tr>\n<td>border-right-width</td>\n<td>Sets the width of the right border</td>\n<td>1</td>\n</tr>\n<tr>\n<td>border-style</td>\n<td>Sets the style of the four borders</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nborder-style: none \n|\n hidden \n|\n dotted \n|\n dashed \n|\n solid \n|\n double \n|\n groove \n|\n ridge \n|\n inset \n|\n outset \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>border-top</td>\n<td>Sets all the top border properties in one declaration</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nborder-top: border-width border-style border-color \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>border-top-color</td>\n<td>Sets the color of the top border</td>\n<td>1</td>\n</tr>\n<tr>\n<td>border-top-left-radius</td>\n<td>Defines the shape of the border of the top-left corner</td>\n<td>3</td>\n</tr>\n<tr>\n<td>border-top-right-radius</td>\n<td>Defines the shape of the border of the top-right corner</td>\n<td>3</td>\n</tr>\n<tr>\n<td>border-top-style</td>\n<td>Sets the style of the top border</td>\n<td>1</td>\n</tr>\n<tr>\n<td>border-top-width</td>\n<td>Sets the width of the top border</td>\n<td>1</td>\n</tr>\n<tr>\n<td>border-width</td>\n<td>Sets the width of the four borders</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nborder-width: medium \n|\n thin \n|\n thick \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>box-decoration-break</td>\n<td>Sets the behavior of the background and border of an element at page-break, or, for in-line elements, at line-break.</td>\n<td>3</td>\n</tr>\n<tr>\n<td>box-shadow</td>\n<td>Attaches one or more drop-shadows to the box</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nbox-shadow: none \n|\n h-shadow v-shadow blur spread color \n|\n inset \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Basic Box Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>bottom</td>\n<td>Specifies the bottom position of a positioned element</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nbottom: auto \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>clear</td>\n<td>Specifies which sides of an element where other floating elements are not allowed</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nclear: none \n|\n left \n|\n right \n|\n both \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>clip</td>\n<td>Clips an absolutely positioned element</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nclip: auto \n|\n shape \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>display</td>\n<td>Specifies how a certain HTML element should be displayed</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\ndisplay: inline \n|\n block \n|\n flex \n|\n inline-block \n|\n inline-flex \n|\n inline-table \n|\n list-item \n|\n run-in \n|\n table \n|\n table-caption \n|\n table-column-group \n|\n table-header-group \n|\n table-footer-group \n|\n table-row-group \n|\n table-cell \n|\n table-column \n|\n table-row \n|\n none \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>float</td>\n<td>Specifies whether or not a box should float</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nfloat: none \n|\n left \n|\n right \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>height</td>\n<td>Sets the height of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nheight: auto \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>left</td>\n<td>Specifies the left position of a positioned element</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nleft: auto \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>margin</td>\n<td>Sets all the margin properties in one declaration</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nmargin: length \n|\n auto \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>margin-bottom</td>\n<td>Sets the bottom margin of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td>margin-left</td>\n<td>Sets the left margin of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td>margin-right</td>\n<td>Sets the right margin of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td>margin-top</td>\n<td>Sets the top margin of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td>max-height</td>\n<td>Sets the maximum height of an element</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nmax-height: none \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>max-width</td>\n<td>Sets the maximum width of an element</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nmax-width: none \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>min-height</td>\n<td>Sets the minimum height of an element</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nmin-height: none \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>min-width</td>\n<td>Sets the minimum width of an element</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nmin-width: none \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>overflow</td>\n<td>Specifies what happens if content overflows an element's box</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\noverflow: visible \n|\n hidden \n|\n scroll \n|\n auto \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>overflow-x</td>\n<td>Specifies whether or not to clip the left/right edges of the content, if it overflows the element's content area</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\noverflow: visible \n|\n hidden \n|\n scroll \n|\n auto \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>overflow-y</td>\n<td>Specifies whether or not to clip the top/bottom edges of the content, if it overflows the element's content area</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\noverflow: visible \n|\n hidden \n|\n scroll \n|\n auto \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>padding</td>\n<td>Sets all the padding properties in one declaration</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\npadding: length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>padding-bottom</td>\n<td>Sets the bottom padding of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td>padding-left</td>\n<td>Sets the left padding of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td>padding-right</td>\n<td>Sets the right padding of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td>padding-top</td>\n<td>Sets the top padding of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td>position</td>\n<td>Specifies the type of positioning method used for an element (static, relative, absolute or fixed)</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nposition: static \n|\n absolute \n|\n fixed \n|\n relative \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>right</td>\n<td>Specifies the right position of a positioned element</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nright: auto \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>top</td>\n<td>Specifies the top position of a positioned element</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\ntop: auto \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>visibility</td>\n<td>Specifies whether or not an element is visible</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nvisibility: visible \n|\n hidden \n|\n collapse \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>width</td>\n<td>Sets the width of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nwidth: auto \n|\n value \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>vertical-align</td>\n<td>Sets the vertical alignment of an element</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nvertical-align: baseline \n|\n length \n|\n sub \n|\n super \n|\n top \n|\n text-top \n|\n middle \n|\n bottom \n|\n text-bottom \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>z-index</td>\n<td>Sets the stack order of a positioned element</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nz-index: auto \n|\n number \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Flexible Box Layout</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>align-content</td>\n<td>Specifies the alignment between the lines inside a flexible container when the items do not use all available space</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nalign-content: stretch \n|\n center \n|\n flex-start \n|\n flex-end \n|\n space-between \n|\n space-around \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>align-items</td>\n<td>Specifies the alignment for items inside a flexible container</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nalign-items: stretch \n|\n center \n|\n flex-start \n|\n flex-end \n|\n baseline \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>align-self</td>\n<td>Specifies the alignment for selected items inside a flexible container</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nalign-self: auto \n|\n stretch \n|\n center \n|\n flex-start \n|\n flex-end \n|\n baseline \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>flex</td>\n<td>Specifies the length of the item, relative to the rest</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nflex: flex-grow flex-shrink flex-basis \n|\n auto \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>flex-basis</td>\n<td>Specifies the initial length of a flexible item</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nflex-basis: number \n|\n auto \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>flex-direction</td>\n<td>Specifies the direction of the flexible items</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nflex-direction: row \n|\n row-reverse \n|\n column \n|\n column-reverse \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>flex-flow</td>\n<td>A shorthand property for the flex-direction and the flex-wrap properties</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nflex-flow: flex-direction flex-wrap \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>flex-grow</td>\n<td>Specifies how much the item will grow relative to the rest</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nflex-grow: number \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>flex-shrink</td>\n<td>Specifies how the item will shrink relative to the rest</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nflex-shrink: number \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>flex-wrap</td>\n<td>Specifies whether the flexible items should wrap or not</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nflex-wrap: nowrap \n|\n wrap \n|\n wrap-reverse \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>justify-content</td>\n<td>Specifies the alignment between the items inside a flexible container when the items do not use all available space</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\njustify-content: flex-start \n|\n flex-end \n|\n center \n|\n space-between \n|\n space-around \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>order</td>\n<td>Sets the order of the flexible item, relative to the rest</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\norder: number \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Text Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>letter-spacing</td>\n<td>Increases or decreases the space between characters in a text</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nletter-spacing: normal \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>line-height</td>\n<td>Sets the line height</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nline-height: normal \n|\n number \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>tab-size</td>\n<td>Specifies the length of the tab-character</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ntab-size: number \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>text-align</td>\n<td>Specifies the horizontal alignment of text</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\ntext-align: left \n|\n right \n|\n center \n|\n justify \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>text-align-last</td>\n<td>Describes how the last line of a block or a line right before a forced line break is aligned when text-align is justify</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ntext-align-last: auto \n|\n left \n|\n right \n|\n center \n|\n justify \n|\n start \n|\n end \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>text-decoration</td>\n<td>Specifies the decoration added to text</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\ntext-decoration: none \n|\n underline \n|\n overline \n|\n line-through \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>text-indent</td>\n<td>Specifies the indentation of the first line in a text-block</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\ntext-indent: length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>text-transform</td>\n<td>Controls the capitalization of text</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\ntext-transform: none \n|\n capitalize \n|\n uppercase \n|\n lowercase \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>white-space</td>\n<td>Specifies how white-space inside an element is handled</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nwhite-space: normal \n|\n nowrap \n|\n pre \n|\n pre-line \n|\n pre-wrap \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>word-spacing</td>\n<td>Increases or decreases the space between words in a text</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nword-spacing: normal \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>word-wrap</td>\n<td>Allows long, unbreakable words to be broken and wrap to the next line</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nword-wrap: normal \n|\n break-word \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Font Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>@font-face</td>\n<td>A rule that allows websites to download and use fonts other than the web-safe fonts</td>\n<td>3</td>\n</tr>\n<tr>\n<td>font</td>\n<td>Sets all the font properties in one declaration</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nfont: font-style font-variant font-weight font-size/line-height font-family \n|\n caption \n|\n icon \n|\n menu \n|\n message-box \n|\n small-caption \n|\n status-bar \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>font-family</td>\n<td>Specifies the font family for text</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nfont-family: family-name \n|\n generic-family \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>font-size</td>\n<td>Specifies the font size of text</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nfont-size:medium \n|\n xx-small \n|\n x-small \n|\n small \n|\n large \n|\n x-large \n|\n xx-large \n|\n smaller \n|\n larger \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>font-style</td>\n<td>Specifies the font style for text</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nfont-style: normal \n|\n italic \n|\n oblique \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>font-variant</td>\n<td>Specifies whether or not a text should be displayed in a small-caps font</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nfont-variant: normal \n|\n small-caps \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>font-weight</td>\n<td>Specifies the weight of a font</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nfont-weight: normal \n|\n bold \n|\n bolder \n|\n lighter \n|\n number \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Table Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>border-collapse</td>\n<td>Specifies whether or not table borders should be collapsed</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nborder-collapse: separate \n|\n collapse \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>border-spacing</td>\n<td>Specifies the distance between the borders of adjacent cells</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nborder-spacing: length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>caption-side</td>\n<td>Specifies the placement of a table caption</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\ncaption-side: top \n|\n bottom \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>empty-cells</td>\n<td>Specifies whether or not to display borders and background on empty cells in a table</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\nempty-cells: show \n|\n hide \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>table-layout</td>\n<td>Sets the layout algorithm to be used for a table</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\ntable-layout: auto \n|\n fixed \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Lists and Counters Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>counter-increment</td>\n<td>Increments one or more counters</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\ncounter-increment: none \n|\n id \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>counter-reset</td>\n<td>Creates or resets one or more counters</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\ncounter-reset: none \n|\n name number \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>list-style</td>\n<td>Sets all the properties for a list in one declaration</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\n list-style: list-style-type list-style-position list-style-image \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>list-style-image</td>\n<td>Specifies an image as the list-item marker</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nlist-style-image: none \n|\n url \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>list-style-position</td>\n<td>Specifies if the list-item markers should appear inside or outside the content flow</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nlist-style-position: inside \n|\n outside \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>list-style-type</td>\n<td>Specifies the type of list-item marker</td>\n<td>1</td>\n</tr>\n<tr>\n<td><code>\nlist-style-type: disc \n|\n armenian \n|\n circle \n|\n cjk-ideographic \n|\n decimal \n|\n decimal-leading-zero \n|\n georgian \n|\n hebrew \n|\n hiragana \n|\n hiragana-iroha \n|\n katakana \n|\n katakana-iroha \n|\n lower-alpha \n|\n lower-greek \n|\n lower-latin \n|\n lower-roman \n|\n none \n|\n square \n|\n upper-alpha \n|\n upper-greek \n|\n upper-latin \n|\n upper-roman \n|\n initial \n|\n inherit\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Animation Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>@keyframes</td>\n<td>Specifies the animation code</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\n@keyframes animationname {keyframes-selector {css-styles;}}\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>animation</td>\n<td>A shorthand property for all the animation properties (except animation-play-state and animation-fill-mode)</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nanimation: name duration timing-function delay iteration-count direction fill-mode play-state;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>animation-delay</td>\n<td>Specifies a delay for the start of an animation</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nanimation-delay: time \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>animation-direction</td>\n<td>Specifies whether or not the animation should play in reverse on alternate cycles</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nanimation-direction: normal \n|\n reverse \n|\n alternate \n|\n alternate-reverse \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>animation-duration</td>\n<td>Specifies how many seconds or milliseconds an animation takes to complete one cycle</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nanimation-duration: time \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>animation-fill-mode</td>\n<td>Specifies a style for the element when the animation is not playing (when it is finished, or when it has a delay)</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nanimation-fill-mode: none \n|\n forwards \n|\n backwards \n|\n both \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>animation-iteration-count</td>\n<td>Specifies the number of times an animation should be played</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nanimation-iteration-count: number \n|\n infinite \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>animation-name</td>\n<td>Specifies the name of the \n<code class=\"language-text\">@keyframes</code>\n animation</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nanimation-name: keyframename \n|\n none \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>animation-play-state</td>\n<td>Specifies whether the animation is running or paused</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nanimation-play-state: paused \n|\n running \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>animation-timing-function</td>\n<td>Specifies the speed curve of an animation</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nanimation-timing-function: linear \n|\n ease \n|\n ease-in \n|\n ease-out \n|\n ease-in-out \n|\n step-start \n|\n step-end \n|\n steps(int,start \n|\n end) \n|\n cubic-bezier(n,n,n,n) \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Transform Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>backface-visibility</td>\n<td>Defines whether or not an element should be visible when not facing the screen</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nbackface-visibility: visible \n|\n hidden \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>perspective</td>\n<td>Specifies the perspective on how 3D elements are viewed</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nperspective: length \n|\n none;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>perspective-origin</td>\n<td>Specifies the bottom position of 3D elements</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nperspective-origin: x-axis y-axis \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>transform</td>\n<td>Applies a 2D or 3D transformation to an element</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ntransform: none \n|\n matrix(n,n,n,n,n,n) \n|\n matrix3d (n,n,n,n,n,n,n,n,n,n,n,n,n,n,n,n) \n|\n translate(x,y) \n|\n translate3d(x,y,z) \n|\n translateX(x) \n|\n translateY(y) \n|\n translateZ(z) \n|\n scale(x,y) \n|\n scale3d(x,y,z) \n|\n scaleX(x) \n|\n scaleY(y) \n|\n scaleZ(z) \n|\n rotate(angle) \n|\n rotate3d(x,y,z,angle) \n|\n rotateX(angle) \n|\n rotateY(angle) \n|\n rotateZ(angle) \n|\n skew(x-angle,y-angle) \n|\n skewX(angle) \n|\n skewY(angle) \n|\n perspective(n) \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>transform-origin</td>\n<td>Allows you to change the position on transformed elements</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ntransform-origin: x-axis y-axis z-axis \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>transform-style</td>\n<td>Specifies how nested elements are rendered in 3D space</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ntransform-style: flat \n|\n preserve-3d \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Transitions Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>transition</td>\n<td>A shorthand property for setting the four transition properties</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ntransition: property duration timing-function delay \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>transition-property</td>\n<td>Specifies the name of the CSS property the transition effect is for</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ntransition-property: none \n|\n all \n|\n property \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>transition-duration</td>\n<td>Specifies how many seconds or milliseconds a transition effect takes to complete</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ntransition-duration: time \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>transition-timing-function</td>\n<td>Specifies the speed curve of the transition effect</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ntransition-timing-function: linear \n|\n ease \n|\n ease-in \n|\n ease-out \n|\n ease-in-out \n|\n step-start \n|\n step-end \n|\n steps(int,start \n|\n end) \n|\n cubic-bezier(n,n,n,n) \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>transition-delay</td>\n<td>Specifies when the transition effect will start</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ntransition-delay: time \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Basic User Interface Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>box-sizing</td>\n<td>Tells the browser what the sizing properties (width and height) should include</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nbox-sizing: content-box \n|\n border-box \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>cursor</td>\n<td>Specifies the type of cursor to be displayed</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\ncursor: alias \n|\n all-scroll \n|\n auto \n|\n cell \n|\n context-menu \n|\n col-resize \n|\n copy \n|\n crosshair \n|\n default \n|\n e-resize \n|\n ew-resize \n|\n grab \n|\n grabbing \n|\n help \n|\n move \n|\n n-resize \n|\n ne-resize \n|\n nesw-resize \n|\n ns-resize \n|\n nw-resize \n|\n nwse-resize \n|\n no-drop \n|\n none \n|\n not-allowed \n|\n pointer \n|\n progress \n|\n row-resize \n|\n s-resize \n|\n se-resize \n|\n sw-resize \n|\n text \n|\n URL \n|\n vertical-text \n|\n w-resize \n|\n wait \n|\n zoom-in \n|\n zoom-out \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>outline</td>\n<td>Sets all the outline properties in one declaration</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\noutline: outline-color outline-style outline-width \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>outline-color</td>\n<td>Sets the color of an outline</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\noutline-color: invert \n|\n color \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>outline-offset</td>\n<td>Offsets an outline, and draws it beyond the border edge</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\noutline-offset: length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>outline-style</td>\n<td>Sets the style of an outline</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\noutline-style: none \n|\n hidden \n|\n dotted \n|\n dashed \n|\n solid \n|\n double \n|\n groove \n|\n ridge \n|\n inset \n|\n outset \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>outline-width</td>\n<td>Sets the width of an outline</td>\n<td>2</td>\n</tr>\n<tr>\n<td><code>\noutline-width: medium \n|\n thin \n|\n thick \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>resize</td>\n<td>Specifies whether or not an element is resizable by the user</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\nresize: none \n|\n both \n|\n horizontal \n|\n vertical \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>text-overflow</td>\n<td>Specifies what should happen when text overflows the containing element</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ntext-overflow: clip \n|\n ellipsis \n|\n string \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2>Multi-column Layout Properties</h2>\n<table>\n<thead>\n<tr>\n<th>Property</th>\n<th>Description</th>\n<th>CSS</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>column-count</td>\n<td>Specifies the number of columns an element should be divided into</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ncolumn-count: number \n|\n auto \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>column-gap</td>\n<td>Specifies the gap between the columns</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ncolumn-gap: length \n|\n normal \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>column-rule</td>\n<td>A shorthand property for setting all the column-rule-\n*\n properties</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ncolumn-rule: column-rule-width column-rule-style column-rule-color \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>column-rule-color</td>\n<td>Specifies the color of the rule between columns</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ncolumn-rule-color: color \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>column-rule-style</td>\n<td>Specifies the style of the rule between columns</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ncolumn-rule-style: none \n|\n hidden \n|\n dotted \n|\n dashed \n|\n solid \n|\n double \n|\n groove \n|\n ridge \n|\n inset \n|\n outset \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>column-rule-width</td>\n<td>Specifies the width of the rule between columns</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ncolumn-rule-width: medium \n|\n thin \n|\n thick \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>column-span</td>\n<td>Specifies how many columns an element should span across</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ncolumn-span: 1 \n|\n all \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>column-width</td>\n<td>Specifies the width of the columns</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ncolumn-width: auto \n|\n length \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>columns</td>\n<td>A shorthand property for setting column-width and column-count</td>\n<td>3</td>\n</tr>\n<tr>\n<td><code>\ncolumns: auto \n|\n column-width column-count \n|\n initial \n|\n inherit;\n</code></td>\n<td></td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h3>Pseudo-Classes</h3>\n<table>\n<thead>\n<tr>\n<th>Name</th>\n<th>Applies to</th>\n<th>Example</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>a:link</td>\n<td>anchor</td>\n<td>A:link, A.classname:link</td>\n</tr>\n<tr>\n<td>a:active</td>\n<td>anchor</td>\n<td>A:active, A.classname:active</td>\n</tr>\n<tr>\n<td>a:visited</td>\n<td>anchor</td>\n<td>A:visited, A.classname:visited</td>\n</tr>\n<tr>\n<td>e:hover</td>\n<td>all</td>\n<td>P:hover, P.classname:hover</td>\n</tr>\n</tbody>\n</table>\n<h3>Pseudo Elements</h3>\n<table>\n<thead>\n<tr>\n<th>Pseudo-elements</th>\n<th>Applies to</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>E:before</td>\n<td>block</td>\n</tr>\n<tr>\n<td>E:after</td>\n<td>block</td>\n</tr>\n<tr>\n<td>E:first-letter</td>\n<td>block</td>\n</tr>\n<tr>\n<td>E:first-line</td>\n<td>block</td>\n</tr>\n</tbody>\n</table>\n<h3>CSS Units</h3>\n<h4>Absolute Lengths</h4>\n<p>The absolute length units are fixed and a length expressed in any of these will appear as exactly that size.</p>\n<p>Absolute length units are not recommended for use on screen, because screen sizes vary so much. However, they can be used if the output medium is known, such as for print layout.</p>\n<table>\n<thead>\n<tr>\n<th>Unit</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>cm</td>\n<td>centimeters</td>\n</tr>\n<tr>\n<td>mm</td>\n<td>millimeters</td>\n</tr>\n<tr>\n<td>in</td>\n<td>inches (1in = 96px = 2.54cm)</td>\n</tr>\n<tr>\n<td>px \n*</td>\n<td>pixels (1px = 1/96th of 1in)</td>\n</tr>\n<tr>\n<td>pt</td>\n<td>points (1pt = 1/72 of 1in)</td>\n</tr>\n<tr>\n<td>pc</td>\n<td>picas (1pc = 12 pt)</td>\n</tr>\n</tbody>\n</table>\n<h4>Relative Lengths</h4>\n<p>Relative length units specify a length relative to another length property. Relative length units scales better between different rendering mediums.</p>\n<table>\n<thead>\n<tr>\n<th>Unit</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>em</td>\n<td>Relative to the font-size of the element (2em means 2 times the size of the current font)</td>\n</tr>\n<tr>\n<td>ex</td>\n<td>Relative to the x-height of the current font (rarely used)</td>\n</tr>\n<tr>\n<td>ch</td>\n<td>Relative to width of the \"0\" (zero)</td>\n</tr>\n<tr>\n<td>rem</td>\n<td>Relative to font-size of the root element</td>\n</tr>\n<tr>\n<td>vw</td>\n<td>Relative to 1% of the width of the viewport\n*</td>\n</tr>\n<tr>\n<td>vh</td>\n<td>Relative to 1% of the height of the viewport\n*</td>\n</tr>\n<tr>\n<td>vmin</td>\n<td>Relative to 1% of viewport's\n*\n smaller dimension</td>\n</tr>\n<tr>\n<td>vmax</td>\n<td>Relative to 1% of viewport's\n*\n larger dimension</td>\n</tr>\n<tr>\n<td>%</td>\n<td>A percent of something</td>\n</tr>\n</tbody>\n</table>\n<p>Note: Tip: The em and rem units are practical in creating perfectly scalable layout! * Viewport = the browser window size. If the viewport is 50cm wide, 1vw = 0.5cm.</p>\n<h3>Colors</h3>\n<table>\n<thead>\n<tr>\n<th>Unit</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>color_name</td>\n<td>A color name (red)</td>\n</tr>\n<tr>\n<td>rgb(x,x,x)</td>\n<td>A rgb value (rgb(255,0,0))</td>\n</tr>\n<tr>\n<td>rgb(y%, y%, y%)</td>\n<td>A rgb percentage value (rgb(100%,0%,0%))</td>\n</tr>\n<tr>\n<td>#rrggbb</td>\n<td>A hex number ( #ff0000).</td>\n</tr>\n</tbody>\n</table>\n<style>\ntable {width: 100%}\ntable tr td:first-of-type {white-space: nowrap;width: 20%;}\ntable tr td:nth-child(2) {width: 70%}\ntable:first-of-type tr td:nth-child(2) {width: auto}\ntable:first-of-type tr td:nth-child(3) {width: auto}\ntable tr td:nth-child(3) {width: 10%}\ntd {empty-cells: show}\n</style>","frontmatter":{"lesson":"CSS3 Reference","title":"Lesson 3","lessonId":3,"downloads":null}},"allMarkdownRemark":{"edges":[{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/3/index.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 3","lesson":"Introduction to CSS"},"fields":{"slug":"/3/index.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/3/selectors.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 3","lesson":"CSS Selectors"},"fields":{"slug":"/3/selectors.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/3/css.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 3","lesson":"CSS3 Reference"},"fields":{"slug":"/3/css.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/3/homework.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 3","lesson":"Homework 3"},"fields":{"slug":"/3/homework.html"}}}]}},"pathContext":{"slug":"/3/css.html","title":"Lesson 3"}}

